import React, { Component } from 'react';

function linearScale(x, x1, x2, y1, y2) {
  return (y2-y1)*(x-x1)/(x2-x1) + y1;
}

function linearScaleAndClamp(x, x1, x2, y1, y2) {
  return linearScale(x, x1, x2, y1, y2);
}

function tempToColor(temp) {
  var r = linearScaleAndClamp(temp, 20, 60, 0, 255);
  var g = 0;
  var b = linearScaleAndClamp(temp, 20, 60, 255, 0);
  var color = 'rgb('+r.toFixed()+','+g.toFixed()+','+b.toFixed()+')';
  console.log("Color: ", color);
  return color;
}

