import React, { Component } from 'react';
import './App.css';
import Mimic from './Mimic';

var mqtt = require('mqtt')
var client  = mqtt.connect('wss://m21.cloudmqtt.com:33266', {username: 'zznyoqdf', password: 'xyt1r5I2QvH5'})

class App extends Component {

  state = {
    "tank-top-temperature": 60,
    "tank-bottom-temperature": 40,
    "collector-temperature": 20
  }

  componentDidMount() {
    console.log("Subscribing");
    client.subscribe('solar-water/tank-top-temperature');
    client.subscribe('solar-water/tank-bottom-temperature');
    client.subscribe('solar-water/collector-temperature');
    client.on('message', (topic, message) => {
      console.log(topic, message.toString());
      const [,propName] = topic.split('/');
      const value = parseFloat(message.toString());
      this.setState({[propName]: value});
    })
  }

  render() {
    return (
      <div className="App">
        <Mimic className="App_mimic" topTemp={this.state['tank-top-temperature']} bottomTemp={this.state['tank-bottom-temperature']} collectorTemp={this.state['collector-temperature']}/>
      </div>
    );
  }
}

export default App;
